.loan-calculator-acf {
  .tabs-loan{
    background-color: transparent;
  }
  .container {
    padding-top: 0px;
    padding-bottom: 0px;
    margin-top: -30px;

    .title {
      font-style: normal;
      font-weight: 300;
      font-size: 2rem;
      line-height: 100%;
      text-align: center;
      color: #3A4454;

      @media (min-width: 767.98px) {
        font-size: 40px;
      }
    }

  }

  .tabs-loan__content {
    display: block;
  }

  .counter_percent .counter__content:after {
    content: '.00 %';
  }

}


.loan-calculator-acf {

  .tabs-loan__wrapper {
    position: relative;
    border-radius: 10px;
    overflow: hidden;

    @media (max-width: 1199.98px) {
      margin-left: 0;
      margin-right: 0;
    }

    &::before {
      position: absolute;
      content: '';
      width: 100%;
      height: 84px;
      box-shadow: 0px 0px 60px rgba(15, 35, 52, 0.35);
      z-index: -1;
    }
  }
}